* {
    box-sizing: border-box;
}

.wrap-page {
    height: 89vh;
}

h1 {
    margin: 2.5rem 0 .5rem;
    text-align: center;
}

h3 {
    margin: 0;
    text-align: center;
    padding-bottom: 1rem;
    position: relative;

    ::after {
        content: "";
        position: absolute;
        bottom: 0;
        border: 1px solid red;
    }
}

.container {
    max-width: 1300px;
    margin: auto;
    display: flex;
    padding: 2rem 4rem 4rem;
    height: 100%;

    &_col-left {
        flex-basis: 40%;
        background-color: #fff;

        textarea {
            width: 100%;
            height: 93.5%;
            outline: none;
            border: none;
            overflow-y: auto;
            resize: none;
            padding: 0 1rem 0;
            font-size: .8rem;
        }
    }

    &_col-right {
        flex-basis: 60%;
        background-color: #e8e8e4;
        overflow-y: auto;

        &_previewer {
            padding: 0 1rem;
            font-size: 1rem;
        }
    }
}

@media screen and (max-width: 750px) and (min-width: 360px) {
    .wrap-page {
        height: auto;
    }

    .container {
        flex-direction: column;
        padding: 1rem;

        &_col-left {

            textarea {
                height: 30vh;
            }
        }

        &_col-right {
            margin-top: 1rem;

            &_previewer {
                height: 40vh;
                overflow-y: auto;
            }
        }
    }
}